.hero{width:100%;max-width:1200px;margin:70px auto;padding:0 20px;transition:all .3s ease;opacity:.97}@media(max-width: 600px){.hero{text-align:center;padding:0}}.hero:hover{opacity:1}.hero__content{display:flex;justify-content:space-between;align-items:center;gap:48px}@media(max-width: 1250px){.hero__content{gap:100px;justify-content:flex-start}}@media(max-width: 1024px){.hero__content{gap:100px;justify-content:flex-start}}@media(max-width: 768px){.hero__content{flex-direction:column;text-align:center;gap:32px;padding:20px 0}}.hero__text{flex:1;max-width:600px}@media(max-width: 1024px){.hero__text{max-width:50%}}@media(max-width: 768px){.hero__text{max-width:80%}}.hero__text-greeting{font-size:clamp(1rem,2.5vw,1.25rem);letter-spacing:1px;margin-bottom:16px;text-transform:uppercase;font-weight:500;color:#1a1a1a}.hero__text-greeting span{color:#009b15;font-weight:600}.hero__text-title{font-size:clamp(2rem,4vw,2.625rem);line-height:1.2;letter-spacing:1px;margin-bottom:24px;color:#1a1a1a;font-weight:700}.hero__text-title.typing::after{content:"|";animation:blink 1s infinite}.hero__text-title span{color:#009b15;display:inline;margin-left:10px;font-weight:700}.hero__text-description{font-size:clamp(1rem,2vw,1.1rem);line-height:1.6;color:#4a4a4a;margin-bottom:30px}.hero__text-skills{font-size:clamp(1rem,2vw,1.1rem);color:#4a4a4a;margin:0;list-style:none;padding:0}@media(max-width: 768px){.hero__text-skills{text-align:left;display:inline-block;width:100%;max-width:500px}}.hero__text-skills li{margin-bottom:12px;padding-left:24px;position:relative;transition:all .3s ease-out}.hero__text-skills li:hover{transform:translateX(5px);color:#2a2a2a}.hero__text-skills li::before{content:"→";color:#009b15;position:absolute;left:0;transition:all .3s ease;font-weight:bold}.hero__text-skills li:last-child{margin-bottom:0}.hero__image{flex:0 0 auto;width:300px;height:400px;position:relative;border-radius:20px;overflow:hidden;box-shadow:0 10px 30px rgba(0,0,0,.1)}@media(max-width: 1024px){.hero__image{width:250px;height:333px}}@media(max-width: 768px){.hero__image{width:220px;height:293px;margin:0 auto}}@media(max-width: 480px){.hero__image{width:200px;height:267px}}.hero__image:hover img{transform:scale(1.05)}.hero__image img{width:100%;height:100%;object-fit:cover;object-position:center top;display:block;transition:transform .5s ease}@keyframes blink{0%,100%{opacity:1}50%{opacity:0}}